.profile__avatar-holder {
  width: 120px;
  height: 120px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
}

@media (min-width:576px) {
  .profile__avatar-holder {
    margin: 0 30px 0 0;
  }
}

@media (max-width:575px) {
  .profile__avatar-holder {
    margin: 0 0 26px;
  }
}
