@font-face {
  font-family: 'Inter';
  src: url('fonts/Inter-Regular.ttf') format('truetype'),
       url('fonts/Inter-Regular.woff2') format('woff2'),
       url('fonts/Inter-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

